<script>
  import DropDownButton from './DropDownButton.svelte';

  export let data = [];

  // gender, phenotype
  $: inputTypes = [
    {
      name: 'sex',
      possibleValues: [...new Set(data.map(d => d.sex))]
    }
  ];
</script>

<div class="selectionbar-container">
  
    <DropDownButton
      values={inputTypes.find(d => d.name === 'sex').possibleValues}
      on:selection_changed
    />
  
</div>