<script>
  import { onMount } from 'svelte';

  import { csv } from 'd3';

  import ScatterPlot from './components/ScatterPlot.svelte';
  
  import SelectionBar from './components/SelectionBar.svelte';

  const endpoint = 'data/quantilesDF.csv';

  let data = [];
  let plotData = [];

  let selectedSex = 'Boy'; // Note: dirty hack

  onMount(async () => {
    // load data
    data = await csv(endpoint, (d) => {
      return {
        ...d,
        ageI: +d.ageI,
        value: +d.value,
        sex: d.sex === "1" ? "Boy" : "Girl"
      };
    });
  });

  const handleSelectionChange = (selectionProperties) => {
    selectedSex = selectionProperties.detail;
  }
  
  $: plotData = data.filter(d => d.sex === selectedSex);
</script>

<SelectionBar 
  data={data}
  on:selection_changed={handleSelectionChange}
/>

<ScatterPlot
  xySet={plotData}
/>

<style>
</style>
