<script>
  import { createEventDispatcher } from 'svelte';

  export let values = [];

  const dispatch = createEventDispatcher();

  let selectedValue;

  $: dispatch('selection_changed', selectedValue);
</script>

<div class="dropdown-container">
  
  <select bind:value={selectedValue}>
    {#each values as value}
      <option>{value}</option>
    {/each}
  </select>
  
</div>